.todoList {
  font-family: DejaVu Sans Mono, monospace;
    font-size: 130%;
    word-break: break-all;
    min-height: inherit;
    
}


.container {
  margin: auto;

  width: 100%;
  height: auto;
  min-height: 500px;
  max-width: 500px;
  min-width: 250px;
  background: #f1f5f8;
  background-image: radial-gradient(#bfc0c1 7.2%, transparent 0);
  background-size: 25px 25px;
  border-radius: 20px;
  box-shadow: 4px 3px 7px 2px #00000040;
  padding: 1rem;
  box-sizing: border-box;
  position: relative;
    top:20px;
}
.heading {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 1rem;
  font-family: Marker Felt, fantasy;
  /* "Architects Daughter", cursive; */
}
.heading h1 {
  transform: rotate(2deg);
  padding: 0.2rem 1.75rem;
  border-radius: 20% 5% 20% 5%/5% 20% 25% 20%;
  background-color: #d8a443;
  font-size: 1.5rem;
  /* margin-left: 1rem; */
}

.heading.workType h1 {
  background-color: #72cdd3;
  font-family: American Typewriter, serif ;
  transform: rotate(0deg);
  padding:0;
  color: white;
}
.heading.shoppingType h1 {
  background-color: #ffe2bd;
  font-family:  Comic Sans MS, Comic Sans, cursive ;
 
}

.form {
  position: relative;
    left: 30px;
}

.form input {
  box-sizing: border-box;
  background-color: transparent;
  padding: 0.7rem;
  border-bottom-right-radius: 15px 3px;
  border-bottom-left-radius: 3px 15px;
  border: solid 3px transparent;
  border-bottom: dashed 3px #fdcb6e;
  font-family: "Architects Daughter";
  font-size: 1rem;
  color: hsla(260, 2%, 25%, 0.7);
  width: 70%;
  margin-bottom: 20px;
}

.workType.form input {
  border-bottom: dashed 3px #9dd0d4  ;
  
  }
  .workType.form  :focus {
    outline: none !important;
    border: dashed 3px #9dd0d4  ;
 
  }


.todoList  button {
  padding: 0;
  border: none;
  font-family: sans-serif;
  text-decoration: none;
  padding-bottom: 3px;
  border-radius: 5px;
  background-color: #ffeaa7;
}

.workType  button {
background-color: #c2edf0;
}
.todoList button span {
  background: #f1f5f8;
  display: block;
  padding: 0.5rem 1rem;
  border-radius: 5px;
  border: 2px solid hsl(198, 1%, 29%);
}


.spaceItemContent li {
  text-align: left;
  position: relative;
  bottom: 0.25rem;
  left: 0.3rem;
  padding: 0.55rem;
  display:inline-block;
  
}

.control {
  display: flex;
  justify-content: space-evenly;
}
