.checked,
.unchecked {
  position: relative;
  top: 5px;
  margin:3px;
}

.unchecked {
  color: burlywood;
 
}

.bookmark{
  position: relative;
  margin:1.55px;
  top:4px;
 
}

.typeChoices {
  display: flex;
  align-self: stretch;
  flex-wrap: wrap;
  align-content: space-around;
  justify-content: center;
  padding: 10%;
}

.custom-checkbox input[type="checkbox"],
.custom-checkbox .checked {
  display: none;
}

.custom-checkbox input[type="checkbox"]:checked ~ .checked {
  display: inline-block;
  color: #7fcd91;


}

.custom-checkbox input[type="checkbox"]:checked ~ .unchecked {
  display: none;
}
.custom-checkbox input[type="checkbox"]:checked ~ .tick {
  display: inline-block;
  color: #d13100;
  margin: 1.45px
